import React, {useState} from "react";
import {
    Box,
    InputGroup,
    InputRightElement,
    Textarea,
    IconButton, InputLeftElement
} from '@chakra-ui/react';

import {ArrowUpIcon, AddIcon} from "@chakra-ui/icons";
import {useDispatch, useSelector} from "react-redux";
import {addChat} from "../reducers";

export default function ChatRoomContent(props){
    const dispatch = useDispatch();
    let [sendBtnIsDisabled, setSendBtnIsDisabled] = useState(true)


    const sendButton = document.getElementsByClassName('sendButton')[0];
    const messageInput = document.getElementsByClassName('messageInput')[0];

    function onChangeEvent(e){
        if(e.target.value === ''){
            setSendBtnIsDisabled(true);
        } else{
            setSendBtnIsDisabled(false);
        }
    }

    function onKeyDownEvent(e){
        if(e.keyCode === 13 && e.shiftKey === false) {
            submit();
            e.preventDefault();
        }
    }

    function submit(){
        if(messageInput.value === ''){
            return;
        }

        dispatch(addChat({messageID: "10", msgType: "my", time: " 10:23", message: messageInput.value}))

        messageInput.value = '';
        setSendBtnIsDisabled(true);
    }

    return(
        <Box w='full' h='50px' backgroundColor='gray.100'>
            <InputGroup size='md' p='5px 10px'>
                <Textarea
                    className='messageInput'
                    placeholder=' .'
                    m='2px 0px 0px 32px'
                    p='7px 35px 7px 15px'
                    rows='1'
                    fontSize='sm'
                    resize='none'
                    overflowY='auto'
                    borderRadius='2em'
                    css={{
                        '&::-webkit-scrollbar': {
                            width: '4px'
                        },
                        '&::-webkit-scrollbar-track': {
                            width: '6px'
                        },
                        '&::-webkit-scrollbar-thumb': {
                            background: 'transparent',
                            borderRadius: '24px'
                        },
                    }}
                    onKeyDown={onKeyDownEvent}
                    onChange={onChangeEvent}
                    onPaste={onChangeEvent}
                    onInput={onChangeEvent}
                />
                <InputLeftElement w='48px' h='50px'>
                    <Box m='10px 8px'>
                        <Box as='button'
                             w='30px'
                             h='30px'
                             border='2px'
                             borderRadius='30%'
                             borderColor='#8F949C'
                             display='flex'
                        >
                            <AddIcon color='#8F949C' w='10px' h='10px' ml='8px' mt='8px' />
                        </Box>
                    </Box>
                </InputLeftElement>
                <InputRightElement w='48px' h='48px'>
                    <Box m='1px 10px 0px 0px'>
                        <Box as='button' onClick={() => submit()}
                             className={sendBtnIsDisabled ? 'sendButton disabledBtn' : 'sendButton'}
                             w='25px'
                             h='25px'
                             borderRadius='50%'
                             backgroundColor='#67595E'
                             display='flex'
                        >
                            <ArrowUpIcon color='white' w='15px' h='15px' ml='5px' mt='5px' />
                        </Box>
                    </Box>
                </InputRightElement>
            </InputGroup>
        </Box>
    );
}