import {combineReducers} from "redux";
import chatReducer from "./chatReducer";

export default combineReducers({chatReducer})

export const addChat = (item) => {
    return {
        type: "ADD_ITEM",
        payload: item
    }
};

export const deleteChat = (items) => {
    return {
        type:"DELETE_ITEM",
        payload: items
    }
};
