import {Box, Center, Flex, Image, SimpleGrid, Text} from "@chakra-ui/react";
import React from "react";

import {Link} from "react-router-dom";

export default function Navigator(props){
    let highlight1 = false;
    let highlight2 = false;
    if(props.index == 1)
        highlight1 = true;
    else if (props.index == 2)
        highlight2 = true;

    return(
        <SimpleGrid columns={2} spacing={1} gap='0px' w='full' h='59px' color='white' >
            <Link to='/Main'>
                <Box as='button' textAlign='center' w='full' h='full' aria-selected={highlight1}>
                    <Center display='grid'>
                        <SimpleGrid columns={1} spacing={2} gap='0px'>
                            <Image
                                ml='17px'
                                boxSize='20px'
                                objectFit='cover'
                                align='center'
                                src={process.env.PUBLIC_URL + '/resources/icon/user.png'}
                            />
                            <Text fontSize='sm' textColor='black'>
                                
                            </Text>
                        </SimpleGrid>
                    </Center>
                </Box>
            </Link>
            <Link to='/ChatList'>
                <Box as='button' textAlign='center' w='full' h='full' aria-selected={highlight2}>
                    <Center display='grid'>
                        <SimpleGrid columns={1} spacing={2} gap='0px' >
                            <Image
                                ml='3px'
                                boxSize='20px'
                                objectFit='cover'
                                src={process.env.PUBLIC_URL+ '/resources/icon/chat.png'}
                            />
                            <Text fontSize='sm' textColor='black'>
                                
                            </Text>
                        </SimpleGrid>
                    </Center>
                </Box>
            </Link>
        </SimpleGrid>
    );
}