import React, {useState} from "react";
import {
    Avatar, AvatarBadge,
    Box,
    Flex, SimpleGrid,
    Text
} from '@chakra-ui/react';
import shortid from "shortid";

export default function MyChat(props){
    return(
        <Flex p='10px'
             w='full'
             position='relative'
             zIndex='5'
             flex='1'
             height='auto'
             verticalAlign='bottom'
        >
            <Avatar mr='10px' boxSize='2em'
                    src={props.item.pImage}
            >
                <AvatarBadge boxSize='1em' bg='green.500' />
            </Avatar>
            <Box>
                <SimpleGrid display='inline-block' columns={1} spacing={2} gap='0px' maxW='80%'>
                    <Box>
                        <Text fontWeight='semibold'>{props.item.name}</Text>
                    </Box>
                    <Box display='inline-block'
                         backgroundColor='#fff'
                         p='8px 11px'
                         borderRadius='10px'
                         letterSpacing='-.2px'
                    >
                        {props.item.message.split("\n").map((txt) => (
                            <React.Fragment key={"MyChat.f:" + shortid.generate()}>
                                {txt}
                                <br />
                            </React.Fragment>
                        ))}
                    </Box>
                </SimpleGrid>
                <Box display='inline-block'
                     verticalAlign='bottom'
                     p='0px'
                     m='0px 0px 0px 5px'
                     borderRadius='10px'
                >
                    <Text fontSize='0.5rem' textColor='gray.500'>{props.item.time}</Text>
                </Box>
            </Box>
        </Flex>
    );
}