import React, {useEffect, useRef, useState} from "react";
import {
    Box
} from '@chakra-ui/react';
import MyChat from "./MyChat";
import OpponentChat from "./OpponentChat";
import {ChevronDownIcon} from "@chakra-ui/icons";
import {useSelector} from "react-redux";

export default function ChatRoomContent(){
    const chatReducer = useSelector(store => store.chatReducer);

    const messagesEndRef = useRef()

    useEffect(() => {
        ScrollToBottom()
    }, [chatReducer])

    const [bottomBtnVisible, setBottomBtnVisible] = useState(false)
    useEffect(() => {
        setTimeout(() => {
            ScrollToBottom()
        }, 100);
    }, []);

    function ScrollToBottom() {
        if(messagesEndRef.current !== undefined){
            messagesEndRef.current.scrollTop = messagesEndRef.current?.scrollHeight;
        } else{
            console.log('messagesEndRef is undefined')
        }
    }

    function BottomScrollBtnIsVisible() {
        if(messagesEndRef.current !== undefined) {
            if (messagesEndRef.current?.scrollTop + messagesEndRef.current?.clientHeight >= messagesEndRef.current?.scrollHeight) {
                setBottomBtnVisible(false)
            } else {
                setBottomBtnVisible(true)
            }
        }
    }

    return(
        <Box h='calc(100% - 50px)'
             backgroundColor='#f2d28b'
        >
            <Box ref={messagesEndRef}
                  onScroll={BottomScrollBtnIsVisible}
                  w='full'
                  h='full'
                  pt='50px'
                  position='sticky'
                  zIndex='1'
                  bottom='50px'
                  fontSize='0.8rem'
                  overflowY='auto'
                  css={{
                      '&::-webkit-scrollbar': {
                          width: '4px'
                      },
                      '&::-webkit-scrollbar-track': {
                          width: '6px'
                      },
                      '&::-webkit-scrollbar-thumb': {
                          background: 'gray',
                          borderRadius: '24px'
                      }
                  }}
            >

                {
                    chatReducer.map((item, index, array) => {
                        if(item.msgType == "my")
                            return <MyChat key={"cmy:"+index} item={item} />;
                        else
                            return <OpponentChat key={"cop:"+index} item={item} />;
                    })
                }
            </Box>

            {bottomBtnVisible ?
                <Box as='button' onClick={() => ScrollToBottom()}
                     w='40px'
                     h='40px'
                     zIndex='6'
                     position='absolute'
                     backgroundColor='#6E5F60'
                     bottom='70px'
                     right='20px'
                     borderRadius='50%'
                >
                    <ChevronDownIcon w='25px' h='25px' color='white'/>
                </Box> : null
            }
        </Box>
    );
}