import React, {useState} from 'react';
import {Button, useDisclosure} from '@chakra-ui/react'

import {
    Box,
    Collapse,
    Text
} from '@chakra-ui/react';

import { AddIcon, MinusIcon } from '@chakra-ui/icons'

import ProfileListItem from './ProfileListItem'

export default function ProfileList(props){
    const { isOpen, onToggle } = useDisclosure({ defaultIsOpen: props.collapseIsOpen });

    const lists = [];

    for(let i = 0; i < props.profileList.length; i++) {
        lists.push(<ProfileListItem key={i} item={props.profileList[i]} onOpen={props.onOpen}
                                    setDialogUserIdnfr={props.setDialogUserIdnfr}/>);
    }

    return(
        <>
            <Box as='button' onClick={onToggle} w='full' h='22px' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' fontSize={0} lineHeight={0} >
                <Text fontSize='xs' float='left' fontWeight='bold' mt='5px' ml='10px' mb='5px'  color='#858585'>
                    {props.listTitle} ({props.profileList.length})
                </Text>
                {
                    isOpen ?
                    (<MinusIcon id='appIcon' float='right' w='10px' h='10px' marginRight='5px' />) :
                    (<AddIcon id='appIcon' float='right' w='10px' h='10px' marginRight='5px' />)
                }
            </Box>
            <Collapse in={isOpen} animateOpacity>
                <Box color='white' mt='0' mb='0' bg='white'>
                    {lists}
                </Box>
            </Collapse>
        </>
    );
}