import React, {useState} from 'react';
import {
    Avatar,
    AvatarBadge, Button, Center, Collapse,
    Container, Divider, Heading,
    Modal, ModalBody, ModalCloseButton,
    ModalContent, ModalHeader,
    ModalOverlay, SimpleGrid,
    Stack,
    useDisclosure,
    useToast
} from '@chakra-ui/react'

import {
    Box,
    Text,
    Flex
} from '@chakra-ui/react';

import {
    Image
} from '@chakra-ui/react';
import copy from "copy-to-clipboard";
import {AddIcon, ChatIcon, EmailIcon, MinusIcon, PhoneIcon, StarIcon} from "@chakra-ui/icons";

export default function MyProfile(props){
    const toast = useToast()
    const { isOpen : isProfileDetailOpen
        , onOpen : onProfileDetailOpen
        , onClose : onProfileDetailClose
    } = useDisclosure();
    const { isOpen : isProfileImageDetailOpen
        , onOpen : onProfileImageDetailOpen
        , onClose : onProfileImageDetailClose
    } = useDisclosure();
    let isCommant = [];

    if(props.profileList.commant !== null && props.profileList.commant !== '')
        isCommant.push(<Box key={props.profileList.userIdnfr} w='auto' h='1.8rem' maxW='60%' mr='5px' mt='10px' p='7px 10px' borderRadius='30px' backgroundColor='gray.300' float='right' display='grid' >
            <Text fontSize='0.6rem' color='black' isTruncated>{props.profileList.commant}</Text>
        </Box>)

    const handleCopyClipBoard = async (text) => {
        try {
            copy(text);
            toast({
                title: ' .',
                status: 'success',
                duration: 3000,
                isClosable: true
            })
        } catch (error) {
            toast({
                title: '  !' ,
                description: '' + error,
                status: 'error',
                duration: 10000,
                isClosable: true
            })
        }
    };

    return(
        <>
            <Box as='button' w='full' h='22px' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' fontSize={0} lineHeight={0} >
                <Text fontSize='xs' float='left' fontWeight='bold' mt='5px' ml='10px' mb='5px'  color='#858585'>
                    {props.listTitle}
                </Text>
            </Box>

            <Box>
                <Box color='white' mt='0' mb='0' bg='white'>
                    <Flex as='button' onClick={onProfileDetailOpen} w='full' color='white' pt='5px' pb='5px' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' textAlign='left' >
                        <Box m='5px' width='50px'>
                            <Stack direction='row' spacing={4}>
                                <Avatar ml='5px' boxSize='2em'
                                        src={props.profileList.pImage}
                                >
                                    <AvatarBadge boxSize='1em' bg='green.500' />
                                </Avatar>
                            </Stack>
                        </Box>
                        <Flex w='full' >
                            <Container w='auto' maxW='50%' p='5px' m={0} textAlign='left' >
                                <Text w='100%' fontSize='15px' fontWeight='bold' color='black' marginLeft='5px'>{props.profileList.name}</Text>
                                <Text w='100%' fontSize='11px' color='gray.500' fontWeight='bold' marginLeft='5px'>{props.profileList.deptName} / {props.profileList.company}</Text>
                            </Container>
                            <Box flex={1}></Box>
                            {isCommant}
                        </Flex >
                        <Modal isOpen={isProfileDetailOpen} onClose={onProfileDetailClose}>
                            <ModalOverlay />
                            <ModalContent>
                                <ModalCloseButton />
                                <ModalBody>
                                    <Box p='30px 30px 10px 30px'>
                                        <Center>
                                            {
                                                props.profileList.pImage === '' ? <Avatar ml='5px' boxSize='5em' src={props.profileList.pImage}></Avatar> :
                                                    <Avatar as='button' onClick={onProfileImageDetailOpen} ml='5px' boxSize='5em' src={props.profileList.pImage}></Avatar>
                                            }
                                        </Center>
                                        <Center mt='0.5rem'>
                                            <Heading as='h5' size='sm'>
                                                {props.profileList.name}
                                            </Heading>
                                        </Center>
                                        {
                                            props.profileList.commant !== '' ?
                                                <Center backgroundColor='gray.200' mt='10px' p='10px 10px' borderRadius='10px' wordBreak='break-all'>
                                                    <Text fontSize='xs'>{props.profileList.commant}</Text>
                                                </Center> : null
                                        }
                                    </Box>
                                    <Divider />
                                    <Box p='10px 30px'>
                                        <SimpleGrid columns={2} spacing={1} gap='0px'>
                                            <Box m='5px'>
                                                <Heading as='h5' size='xs'> <a href={"tel:" + props.profileList.telno1} className='textRightIcon'><PhoneIcon w={3} h={3}/></a> </Heading>
                                                <Text as='button' onClick={() => handleCopyClipBoard(props.profileList.telno1)} fontSize='sm' color='gray.500' isTruncated>{props.profileList.telno1}</Text>
                                            </Box>
                                            <Box m='5px'>
                                                <Heading as='h5' size='xs'> <a href={"tel:" + props.profileList.telno2} className='textRightIcon'><PhoneIcon w={3} h={3}/></a> </Heading>
                                                <Text as='button' onClick={() => handleCopyClipBoard(props.profileList.telno2)} fontSize='sm' color='gray.500' isTruncated>{props.profileList.telno2}</Text>
                                            </Box>
                                        </SimpleGrid>
                                        <SimpleGrid columns={2} spacing={1} gap='0px'>
                                            <Box m='5px'>
                                                <Heading as='h5' size='xs'></Heading>
                                                <Text fontSize='sm' color='gray.500' isTruncated>{props.profileList.responsibilities}</Text>
                                            </Box>
                                            <Box m='5px'>
                                                <Heading as='h5' size='xs'> <a href={"mailto:" + props.profileList.email} className='textRightIcon'><EmailIcon w={3} h={3}/></a></Heading>
                                                <Text as='button' onClick={() => handleCopyClipBoard(props.profileList.email)} fontSize='sm' color='gray.500' isTruncated>{props.profileList.email}</Text>
                                            </Box>
                                        </SimpleGrid>
                                    </Box>
                                    <Divider />
                                    <Box p='10px 30px 30px 30px'>
                                        <SimpleGrid columns={2} spacing={1} gap='5px' >
                                            <Button w='full' h='full' p='10px 10px' leftIcon={<ChatIcon />} colorScheme='cyan' variant='outline'>
                                                
                                            </Button>
                                            <Button w='full' h='full' p='10px 10px' leftIcon={<StarIcon />} colorScheme='cyan' variant='outline'>
                                                
                                            </Button>
                                        </SimpleGrid>
                                    </Box>
                                </ModalBody>
                            </ModalContent>
                        </Modal>

                        <Modal isOpen={isProfileImageDetailOpen} onClose={onProfileImageDetailClose} size='full'>
                            <ModalOverlay />
                            <ModalContent>
                                <ModalHeader>  ({props.profileList.name})</ModalHeader>
                                <ModalCloseButton />
                                <Divider />
                                <ModalBody>
                                    <Flex justifyContent='center' alignItems='center' height='75vh'>
                                        <Image src={props.profileList.pImage} display='block' margin='auto' maxW='100%' h='auto' />
                                    </Flex>
                                </ModalBody>
                            </ModalContent>
                        </Modal>
                    </Flex>
                </Box>
            </Box>
        </>
    );
}