import React, {useState} from "react";
import {
    Box,
    Flex,
    Text
} from '@chakra-ui/react';
import shortid from "shortid";

export default function MyChat(props){
    return(
        <Flex p='10px'
             w='full'
             position='relative'
             zIndex='5'
             flex='1'
             height='auto'
             verticalAlign='bottom'
        >
            <Box flex={1}></Box>
            <Box display='inline-block'
                 verticalAlign='bottom'
                 p='0px'
                 m='0px 5px 0px 0px'
                 borderRadius='10px'
                 width='50px'
            >
                <Text fontSize='0.5rem' position='absolute' bottom='12px' textColor='gray.500' isTruncated>{props.item.time}</Text>
            </Box>
            <Box display='inline-block'
                 backgroundColor='#E7F2F8'
                 p='8px 11px'
                 borderRadius='10px'
                 maxW='75%'
                 letterSpacing='-.2px'
            >
                {props.item.message.split("\n").map((txt) => (
                    <React.Fragment key={"MyChat.f:" + shortid.generate()}>
                        {txt}
                        <br />
                    </React.Fragment>
                ))}
            </Box>
        </Flex>
    );
}