import React from 'react';
import {
    Box,
    IconButton,
    Drawer,
    DrawerOverlay,
    DrawerContent,
    DrawerHeader,
    DrawerBody,
    Popover,
    PopoverTrigger,
    PopoverContent,
    PopoverHeader,
    PopoverArrow,
    PopoverCloseButton,
    PopoverBody,
    useDisclosure,
    Heading,
    CloseButton
} from '@chakra-ui/react';

import {ChevronLeftIcon, HamburgerIcon} from '@chakra-ui/icons'
import {Link} from "react-router-dom";

export default function ChatRoomHeader(){
    const { isOpen, onOpen, onClose } = useDisclosure()

    return (
        <>
            <Box w='100%' h='50px' bg='#67595E' opacity='0.95' position='absolute' display='inline-block' whiteSpace='nowrap' top='0px' left='0px' zIndex='5'>
                <Link to='/ChatList'>
                    <IconButton
                        display='inline-block'
                        colorScheme='white'
                        w='50px'
                        h='50px'
                        icon={<ChevronLeftIcon w='30px' h='30px' />}
                    />
                </Link>

                <Box textAlign='center' w='calc(100% - 100px)' display='inline-block'>
                    <Heading textColor='white' h='50px' size='xs' display='inline' isTruncated>
                         
                    </Heading>
                </Box>

                <IconButton
                    display='inline'
                    onClick={onOpen}
                    colorScheme='white'
                    float='right'
                    w='50px'
                    h='50px'
                    icon={<HamburgerIcon w='25px' h='25px' />}
                />

                <Drawer onClose={onClose} isOpen={isOpen} size='xs'>
                    <DrawerOverlay />
                    <DrawerContent>
                        <DrawerHeader p='0px' size='lg'>
                            <CloseButton float='right' top='0px' right='0px' w='50px' h='50px' onClick={onClose} />
                        </DrawerHeader>
                        <DrawerBody>

                        </DrawerBody>
                    </DrawerContent>
                </Drawer>
            </Box>
        </>
    );
}