import React, {useRef} from 'react';

import copy from 'copy-to-clipboard';

import {
    Box,
    Text,
    Flex,
    Stack,
    Avatar,
    AvatarBadge,
    Container,
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalBody,
    ModalCloseButton,
    Center,
    useDisclosure,
    useToast,
    Heading,
    Divider,
    SimpleGrid,
    Button, Image
} from '@chakra-ui/react';

import {
    PhoneIcon,
    EmailIcon,
    ChatIcon,
    StarIcon
}from '@chakra-ui/icons'

export default function ProfileListItem(props){
    const toast = useToast()
    const { isOpen : isProfileDetailOpen
          , onOpen : onProfileDetailOpen
          , onClose : onProfileDetailClose
    } = useDisclosure();
    const { isOpen : isProfileImageDetailOpen
        , onOpen : onProfileImageDetailOpen
        , onClose : onProfileImageDetailClose
    } = useDisclosure();
    let isCommant = [];

    if(props.item.commant !== null && props.item.commant !== '')
        isCommant.push(<Box key={props.item.userIdnfr} w='auto' h='1.8rem' maxW='60%' mr='5px' mt='10px' p='7px 10px' borderRadius='30px' backgroundColor='gray.300' float='right' display='grid' >
                          <Text fontSize='0.6rem' color='black' isTruncated>{props.item.commant}</Text>
                       </Box>)

    const handleCopyClipBoard = async (text) => {
        try {
            copy(text);
            toast({
                title: ' .',
                status: 'success',
                duration: 3000,
                isClosable: true
            })
        } catch (error) {
            toast({
                title: '  !' ,
                description: '' + error,
                status: 'error',
                duration: 10000,
                isClosable: true
            })
        }
    };

    return(
        <>
            <Flex as='button' onClick={onProfileDetailOpen} w='full' color='white' pt='5px' pb='5px' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' textAlign='left' >
                <Box m='5px' width='50px'>
                    <Stack direction='row' spacing={4}>
                        <Avatar ml='5px' boxSize='2em'
                                /*name={props.item.name}*/
                                src={props.item.pImage}
                        >
                            <AvatarBadge boxSize='1em' bg='green.500' />
                        </Avatar>
                    </Stack>
                </Box>
                <Flex w='full' >
                    <Container w='auto' maxW='50%' p='5px' m={0} textAlign='left' >
                        <Text w='100%' fontSize='15px' fontWeight='bold' color='black' marginLeft='5px'>{props.item.name}</Text>
                        <Text w='100%' fontSize='11px' color='gray.500' fontWeight='bold' marginLeft='5px'>{props.item.deptName} / {props.item.company}</Text>
                    </Container>
                    <Box flex={1}></Box>
                    {isCommant}
                </Flex >
            </Flex>
            <Modal isOpen={isProfileDetailOpen} onClose={onProfileDetailClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalCloseButton />
                    <ModalBody>
                        <Box p='30px 30px 10px 30px'>
                            <Center>
                                {
                                    props.item.pImage === '' ? <Avatar ml='5px' boxSize='5em' src={props.item.pImage}></Avatar> :
                                        <Avatar as='button' onClick={onProfileImageDetailOpen} ml='5px' boxSize='5em' src={props.item.pImage}></Avatar>
                                }
                            </Center>
                            <Center mt='0.5rem'>
                                <Heading as='h5' size='sm'>
                                    {props.item.name}
                                </Heading>
                            </Center>
                            {
                                props.item.commant !== '' ?
                                <Center backgroundColor='gray.200' mt='10px' p='10px 10px' borderRadius='10px' wordBreak='break-all'>
                                    <Text fontSize='xs'>{props.item.commant}</Text>
                                </Center> : null
                            }
                        </Box>
                        <Divider />
                        <Box p='10px 30px'>
                            <SimpleGrid columns={2} spacing={1} gap='0px'>
                                <Box m='5px'>
                                    <Heading as='h5' size='xs'> <a href={"tel:" + props.item.telno1} className='textRightIcon'><PhoneIcon w={3} h={3}/></a> </Heading>
                                    <Text as='button' onClick={() => handleCopyClipBoard(props.item.telno1)} fontSize='sm' color='gray.500' isTruncated>{props.item.telno1}</Text>
                                </Box>
                                <Box m='5px'>
                                    <Heading as='h5' size='xs'> <a href={"tel:" + props.item.telno2} className='textRightIcon'><PhoneIcon w={3} h={3}/></a> </Heading>
                                    <Text as='button' onClick={() => handleCopyClipBoard(props.item.telno2)} fontSize='sm' color='gray.500' isTruncated>{props.item.telno2}</Text>
                                </Box>
                            </SimpleGrid>
                            <SimpleGrid columns={2} spacing={1} gap='0px'>
                                <Box m='5px'>
                                    <Heading as='h5' size='xs'></Heading>
                                    <Text fontSize='sm' color='gray.500' isTruncated>{props.item.responsibilities}</Text>
                                </Box>
                                <Box m='5px'>
                                    <Heading as='h5' size='xs'> <a href={"mailto:" + props.item.email} className='textRightIcon'><EmailIcon w={3} h={3}/></a></Heading>
                                    <Text as='button' onClick={() => handleCopyClipBoard(props.item.email)} fontSize='sm' color='gray.500' isTruncated>{props.item.email}</Text>
                                </Box>
                            </SimpleGrid>
                        </Box>
                        <Divider />
                        <Box p='10px 30px 30px 30px'>
                            <SimpleGrid columns={2} spacing={1} gap='5px' >
                                <Button w='full' h='full' p='10px 10px' leftIcon={<ChatIcon />} colorScheme='cyan' variant='outline'>
                                    
                                </Button>
                                <Button w='full' h='full' p='10px 10px' leftIcon={<StarIcon />} colorScheme='cyan' variant='outline'>
                                    
                                </Button>
                            </SimpleGrid>
                        </Box>
                    </ModalBody>
                </ModalContent>
            </Modal>

            <Modal isOpen={isProfileImageDetailOpen} onClose={onProfileImageDetailClose} size='full'>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>  ({props.item.name})</ModalHeader>
                    <ModalCloseButton />
                    <Divider />
                    <ModalBody>
                        <Flex justifyContent='center' alignItems='center' height='75vh'>
                            <Image src={props.item.pImage} display='block' margin='auto' w='100%' maxW='700px' h='auto'/>
                        </Flex>
                    </ModalBody>
                </ModalContent>
            </Modal>
        </>
    );
}