import React from 'react';
import {
    Box,
    InputGroup,
    Wrap,
    WrapItem,
    InputLeftElement,
    Input,
    Center,
    Image, Heading, IconButton
} from '@chakra-ui/react';

import {Search2Icon, SearchIcon} from '@chakra-ui/icons'

export default function ChatHeader(){
    return (
        <Box h='100px' bg='#00BCD5'>
            <Wrap px='10px' py='5px' w='100%' spacing='10px'>
                <WrapItem w='100%'>
                    <Box w='100px' h='30px'>
                        <Heading as='h5' size='sm' mt='0.5rem' textColor='white'>
                            
                        </Heading>
                    </Box>
                </WrapItem>
                <WrapItem w='100%'>
                    <Center w='100%' >
                        <InputGroup>
                            <InputLeftElement
                                pointerEvents='none'
                                children={<Search2Icon color='gray.300' />}
                            />
                            <Input bg='white' type='text' placeholder=' ' />
                            <IconButton aria-label='Search' icon={<SearchIcon />} ml='5px'/>
                        </InputGroup>
                    </Center>
                </WrapItem>
            </Wrap>
        </Box>
    );
}