import React from 'react';
import {
    Box,
    InputGroup,
    Wrap,
    WrapItem,
    InputLeftElement,
    Input,
    Center,
    Image,
    IconButton
} from '@chakra-ui/react';

import { Search2Icon, SearchIcon } from '@chakra-ui/icons'

export default function MainHeader(){
    return (
        <Box h='100px' bg='#00BCD5' zIndex='5'>
            <Wrap px='10px' py='5px' w='100%' spacing='10px'>
                <WrapItem w='100%'>
                    <Box w='100px' h='30px'>
                        <Image src={process.env.PUBLIC_URL + '/resources/icon/main_logo.png'} alt=' ' />
                    </Box>
                </WrapItem>
                <WrapItem w='100%'>
                    <Center w='100%' >
                        <InputGroup>
                            <InputLeftElement
                                pointerEvents='none'
                                children={<Search2Icon color='gray.300' />}
                            />
                            <Input bg='white' type='text' placeholder=', , , , ' />
                            <IconButton aria-label='Search' icon={<SearchIcon />} ml='5px'/>
                        </InputGroup>
                    </Center>
                </WrapItem>
            </Wrap>
        </Box>
    );
}