import React from 'react';
import {Link} from "react-router-dom";
import shortid from 'shortid';

import {
    Box,
    Flex,
    SimpleGrid,
    Avatar,
    Heading,
    Text
} from '@chakra-ui/react';

export default function ChatingList(props){

    return(
        <Link to='/ChatingRoom' state={{roomId: props.item.roomId}}>
            <Flex as='button' w='full' h='70px' color='white' pt='5px' pb='5px' borderTop='1px' borderBottom='1px' borderColor='gray.100' >
                <Box h='100%' mt='8px'>
                    <Avatar ml='10px' mr='10px' boxSize='2.2em' src={props.item.pImage}></Avatar>
                </Box>
                <Box w='100%' mt='5px' ml='0px' mr='5px'>
                    <SimpleGrid columns={1} spacing={2} w='100%' gap='0px' pl='3px' >
                        <Box w='100%'>
                            <Flex w='full' color='white' borderTop='0px'>
                                <Heading w='80%' as='h6' size='xs' textAlign='left' textColor='black' isTruncated>
                                    {props.item.name}
                                </Heading>
                                <Text w='20%' pr='5px' textColor='gray.400' textAlign='right' fontWeight='semibold' fontSize='0.7rem'>
                                    {props.item.previewDate}
                                </Text>
                            </Flex>
                        </Box>
                        <Box flex='1'>
                            <Text w='100%' mr='30px' fontSize='xs' textAlign='left' textColor='gray.500' isTruncated>
                                {props.item.previewText.split("\n").map((txt) => (
                                    <React.Fragment key={"ChatingList.f:" + shortid.generate()}>
                                    {txt}
                                    <br />
                                    </React.Fragment>
                                ))}
                            </Text>
                        </Box>
                    </SimpleGrid>
                </Box>
            </Flex>
        </Link>
    );
}